import request from '@/utils/request'
// 加入购物车
export const addCart = function (goodsId, goodsNum, goodsSkuId) {
  return request({
    url: '/cart/add',
    method: 'post',
    data: {
      goodsId,
      goodsNum,
      goodsSkuId
    }
  })
}
// 获取购物车列表
export const getCartList = function () {
  return request({
    url: '/cart/list',
    method: 'get'
  })
}
// 更新购物车商品数量
export const changeCount = function (goodsId, goodsNum, goodsSkuId) {
  return request({
    url: '/cart/update',
    method: 'post',
    data: {
      goodsId,
      goodsNum,
      goodsSkuId
    }
  })
}
// 删除购物车商品
export const delSelect = function (cartIds) {
  return request({
    url: '/cart/clear',
    method: 'post',
    data: {
      cartIds
    }
  })
}
