const INFO_KEY = 'hm_shopping_info'
const HISTORY_KEY = 'hm_history_list'
// 获取用户信息
export const getInfo = function () {
  const defaultObj = { token: '', userId: '' }
  const res = localStorage.getItem(INFO_KEY)
  return res ? JSON.parse(res) : defaultObj
}
// 设置用户信息
export const setInfo = function (obj) {
  localStorage.setItem(INFO_KEY, JSON.stringify(obj))
}
// 移除用户信息
export const removeInfo = function () {
  localStorage.removeItem(INFO_KEY)
}

// 获取历史信息
export const getHistoryList = function () {
  const res = localStorage.getItem(HISTORY_KEY)
  return res ? JSON.parse(res) : []
}
// 设置历史信息
export const setHistoryList = function (arr) {
  localStorage.setItem(HISTORY_KEY, JSON.stringify(arr))
}
