import { changeCount, delSelect, getCartList } from '@/api/cart'

export default {
  namespaced: true,
  state () {
    return {
      cartList: []
    }
  },
  mutations: {
    // 提供一个设置cartList的方法
    setCartList (state, newList) {
      state.cartList = newList
    },
    toggleCheck (state, id) {
      const goods = state.cartList.find(item => item.goods_id === id)
      goods.isChecked = !goods.isChecked
    },
    toggleAllCheck (state, flag) {
      state.cartList.forEach(item => {
        item.isChecked = flag
      })
    },
    changeCount (state, { goodsId, goodsNum }) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.goods_num = goodsNum
    }
  },
  actions: {
    async getCartAction (context) {
      const res = await getCartList()
      res.data.list.forEach(element => {
        element.isChecked = true
      })
      context.commit('setCartList', res.data.list)
    },
    async changeCountAction (context, obj) {
      const { goodsNum, goodsId, goodsSkuId } = obj
      context.commit('changeCount', { goodsId, goodsNum })
      await changeCount(goodsId, goodsNum, goodsSkuId)
    },
    // 删除购物车数据
    async delSelect (context) {
      const selectList = context.getters.selCartList
      const cartIds = selectList.map(item => item.id)
      await delSelect(cartIds)
      context.dispatch('getCartAction')
    }
  },
  getters: {
    // 求所有商品的累计总数
    cartTotal (state) {
      return state.cartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 选中的商品列表
    selCartList (state) {
      return state.cartList.filter(item => item.isChecked)
    },
    // 求选中的商品总数
    selCount (state, getters) {
      return getters.selCartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 求选中商品总价
    selPrice (state) {
      let sum = 0
      for (let i = 0; i < state.cartList.length; i++) {
        if (state.cartList[i].isChecked) sum += state.cartList[i].goods_num * state.cartList[i].goods.goods_price_min
      }
      return sum.toFixed(2)
    },
    // 全选操作
    isAllChecked (state) {
      return state.cartList.every(item => item.isChecked)
    }
  }
}
