<template>
  <div class="count-box">
    <button @click="handleAdd(-1)" class="minus">-</button>
    <input type="text" @change="handleChange" class="inp" :value="value">
    <button @click="handleAdd(1)" class="add">+</button>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      typeof: Number,
      default: 1

    }
  },
  methods: {
    handleAdd (n) {
      if (this.value === 1 && n === -1) return
      this.$emit('input', this.value + n)
    },
    handleChange (e) {
      const num = +e.target.value
      if (isNaN(num) || num < 1) {
        e.target.value = this.value
        return
      }
      this.$emit('input', num)
    }
  }
}
</script>

<style lang="less" scoped>
.count-box{
  width: 110px;
  display: flex;
  .add, .minus{
    width: 30px;
    height: 30px;
    outline: none;
    border: none;
    border-color: #efefef;
  }
  .inp{
    width: 40px;
    height: 30px;
    margin: 0 5px;
    outline: none;
    border: none;
    border-color: #efefef;
    text-align: center;
  }
}

</style>
